<template>
  <v-container class="my-3">
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + queries.practitioner)">
            <v-icon light left>mdi-close-circle-outline</v-icon>
            <span>{{ $t(`App.hardcoded-texts.Cancel`) }}</span>
          </v-btn>
        </v-list-item>
        <v-list-item>
          <v-btn class="success darken-1" dark>
            <v-icon light left>mdi-content-save</v-icon>
            <span>{{ $t(`App.hardcoded-texts.Save`) }}</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>          
      </v-list>
    </v-navigation-drawer>
    <v-card 
      class="mx-auto"
      max-width="700"
      outlined
    >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        PP License Application
      </v-card-title>
      <v-card-text class="my-3">
        <v-autocomplete
          label="License Type" 
          v-model="licenseType" 
          :items="licenseTypes"
          return-object
          outlined 
          hide-details="auto" 
          item-text="display"
          item-value="code"
          dense
          :rules="licensetyperules"
          @change="errors.licensetype = []"
          :disabled="disableAdding"
        />
        <br>
        <v-autocomplete
          label="HP Number" 
          v-model="hpNumber" 
          :items="hpNumbers"
          item-text="number"
          item-value="number"
          return-object
          outlined 
          hide-details="auto" 
          dense
          :rules="hpnumberrules"
          @change="errors.hpnumber = []"
          :disabled="disableAdding"
        />
        <br v-if="hpNumber.profession">
        <v-row class="justify-space-between" v-if="hpNumber.profession">
          <v-col cols="4">
            <span class="font-weight-bold">Profession:</span>
          </v-col>
          <v-col cols="8">
            {{ hpNumber.profession.name }} - {{ hpNumber.profession.code }}
          </v-col>
        </v-row>
        <br>
        <v-menu
          v-model="applicationDateMenu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="applicationDateFormatted"
              label="Application Date*"
              prepend-inner-icon="mdi-calendar"
              readonly
              v-on="on"
              :rules="applicationdaterules"
              outlined
              hide-details="auto"
              dense
              :disabled="disableAdding"
            />
          </template>
          <v-date-picker
            color="secondary"
            :landscape="$vuetify.breakpoint.smAndUp"
            v-model="applicationDate"
            @change="applicationDateFormatted = formatDate(applicationDate)"
            @input="applicationDateMenu = false"
          />
        </v-menu>
        <br>
        <fhir-reference
          field="value[x]:valueReference"
          label="Location"
          targetProfile="http://ihris.org/fhir/StructureDefinition/bw-city"
          targetResource="Location"
          min="1"
          max="1"
          :slotProps="slot"
          :edit="true"
          displayType="tree"
          initialValue="Location/countrybw"
          path="licenseApplication"
          :disabled="disableAdding"
        />
        <v-card 
          class="mx-auto"
          max-width="700"
          outlined
        >
          <v-card-title class="primary white--text font-weight-bold">Submitted Document</v-card-title>
          <v-card-text class="my-3">
            <v-card
              v-for="document in requiredDocuments"
              :key="document.code"
            >
              <v-card-text>
                <v-checkbox
                  :label="document.display"
                  v-model="submittedDocuments"
                  :value="document.code"
                  :disabled="disableAdding"
                />
                <fhir-attachment
                  field="value[x]:valueAttachment"
                  label="Attachment"
                  min="0"
                  max="1"
                  :path="document.code"
                  :slotProps="{source:{path:'.extension:attachment', data: []}}"
                  :edit="true"
                  :disabled="disableAdding"
                />
              </v-card-text>
            </v-card>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>
    <v-btn color="success" @click="save">Save</v-btn>
  </v-container>
</template>
<script>
const querystring = require("querystring");
import { eventBus } from "@/main";
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      disableAdding: false,
      loadingLicenseTypes: false,
      licenseTypes: [],
      licenseType: '',
      hpNumbers: [],
      hpNumber: {},
      applicationDate: '',
      location: '',
      applicationDateFormatted: '',
      applicationDateMenu: false,
      requiredDocuments: [],
      submittedDocuments: [],
      errors: {
        licensetype: [],
        hpnumber: [],
        applicationdate: []
      },
      slot: {
        source: {
          path: ".extension:location",
          data: []
        }
      }
    }
  },
  components: {
    "fhir-reference": () => import(/* webpackChunkName: "fhir-secondary" */ "@/components/fhir/fhir-reference" ),
    "fhir-attachment": () => import(/* webpackChunkName: "fhir-secondary" */ "@/components/fhir/fhir-attachment" ),
  },
  computed: {
    applicationdaterules() {
      return [
        (v) => {
          this.errors.applicationdate = []
          if(!v) {
            this.errors.applicationdate = ["Application date is required"]
            return "Application date is required"
          }
          return true
        }
      ]
    },
    licensetyperules() {
      return [
        (v) => {
          if(!v) {
            this.errors.licensetype = ["License type is required"]
            return "License type is required"
          }
          return true
        }
      ]
    },
    hpnumberrules() {
      return [
        (v) => {
          if(!v) {
            this.errors.licensetype = ["HP number is required"]
            return "HP number is required"
          }
          return true
        }
      ]
    }
  },
  methods: {
    save() {
      let resource = {
        resourceType: "Basic",
        meta: {
          profile: ["http://ihris.org/fhir/StructureDefinition/pp-license-application-profile"]
        },
        extension: [{
          url: "http://ihris.org/fhir/StructureDefinition/pp-location",
          valueReference: {
            reference: this.location
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/bw-profession-subtype",
          valueReference: {
            reference: this.hpNumber.profession.id
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/pp-license-type",
          valueCoding: {
            system: "http://ihris.org/fhir/CodeSystem/pp-license-type-codesystem",
            code: this.licenseType.code,
            display: this.licenseType.display
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/application-date",
          valueDate: this.applicationDate
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/pp-application-status",
          valueCoding: {
            system: "http://ihris.org/fhir/CodeSystem/pp-application-status-codesystem",
            code: "pending",
            display: "Pending"
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
          valueReference: {
            reference: "Practitioner/" + this.queries.practitioner
          }
        }]
      }
      let docsExtension = []
      for(let doc of this.submittedDocuments) {
        let docObj = this.requiredDocuments.find((required) => {
          return required.code === doc
        })
        docsExtension.push({
          url: "http://ihris.org/fhir/StructureDefinition/pp-submitted-document",
          extension: [{
            url: "name",
            valueCoding: {
              system: docObj.system,
              code: docObj.code,
              display: docObj.display
            }
          }]
        })
      }
      this.populateValues(this.$children, docsExtension)
      if(docsExtension.length > 0) {
        resource.extension.push(docsExtension)
      }
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(resource)
      }
      this.overlay = true
      fetch("/site/pp/license_application", opts).then((response) => {
        this.overlay = false;
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Schedule added successfully",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to add schedule."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to add schedule."
        });
      })
    },
    populateValues(children, docsExtension) {
      for (let child of children) {
        if(child.qField && child.value) {
          if(child.qField === "valueAttachment" && this.submittedDocuments.includes(child.path) && child.value.data) {
            let index = docsExtension.findIndex((ext) => {
              return ext.extension.find((doc) => {
                return doc.valueCoding.code === child.path
              })
            })
            docsExtension[index].extension.push({
              url: "attachment",
              valueAttachment: {
                contentType: child.value.contentType,
                data: child.value.data,
                title: child.value.title
              }
            })
          }
        }
        if (child.$children) {
          this.populateValues(child.$children, docsExtension)
        }
      }
    },
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    getLicenseTypes() {
      this.loadingLicenseTypes = true
      this.$fhirutils.expand( 'http://ihris.org/fhir/ValueSet/pp-license-type-valueset' ).then( items => {
        this.licenseTypes = items
        this.loadingLicenseTypes = false
      } ).catch( err => {
        console.log(err)
        this.loadingLicenseTypes = false
      } )
    },
    getRequiredDocs() {
      this.loadingRequiredDocuments = true
      this.$fhirutils.expand( 'http://ihris.org/fhir/ValueSet/pp-submitted-document-valueset' ).then( items => {
        this.requiredDocuments = items
        this.loadingRequiredDocuments = false
      } ).catch( err => {
        console.log(err)
        this.loadingRequiredDocuments = false
      } )
    },
    getHPNumbers() {
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/bhpc-registration-profile&practitioner=Practitioner/" + this.queries.practitioner).then((response) => {
        response.json().then(async(registrations) => {
          for(let registration of registrations.entry) {
            let hp = registration.resource.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/bhpc-hp-number'
            })?.valueString
            let professionRef = registration.resource.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/bw-profession-subtype'
            })?.valueReference?.reference
            let profession = {}
            await fetch("/fhir/" + professionRef).then((response) => {
              response.json().then((prof) => {
                profession.name = prof.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-basic-name'
                })?.valueString
                profession.code = prof.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/bw-subtype-code'
                })?.valueString
              })
            })
            profession.id = professionRef
            this.hpNumbers.push({
              number: hp,
              profession
            })
          }
        })
      })
    },
    getBHPCCurrentStatus() {
      let params = {
        _profile: "http://ihris.org/fhir/StructureDefinition/bhpc-registration-status-profile",
        practitioner: 'Practitioner/' + this.queries.practitioner,
        "statusend:missing": true,
        _sort: "-_id",
        _count: 1
      }
      fetch("/fhir/Basic?" + querystring.stringify(params)).then((response) => {
        response.json().then((status) => {
          if(status.entry && status.entry.length > 0) {
            let statusCode = status.entry[0].resource.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/bhpc-file-status'
            }).valueCoding.code
            if(statusCode !== 'active' && statusCode !== 'upgrade') {
              this.$store.commit("setMessage", {
                type: "error",
                text: "No active BHPC registration found"
              });
              this.disableAdding = true
            }
          }
        }).catch(() => {
          this.$store.commit("setMessage", {
            type: "error",
            text: "No active BHPC registration found"
          });
          this.disableAdding = true
        })
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "No active BHPC registration found"
        });
        this.disableAdding = true
      })
    }
  },
  created() {
    eventBus.$on("licenseApplication", (val) => {
      this.location = val
    })
    this.getBHPCCurrentStatus()
    this.getHPNumbers()
    this.getLicenseTypes()
    this.getRequiredDocs()
  }
}
</script>